import React, {Component} from "react";
import {PropTypes} from "prop-types";

const prepareRules = (rules,translationMap) => {
    return rules.map(rule => {
        var targetNames = rule.targetVases.map(vas => translationMap[vas]);
        var sourceNames = rule.sourceVases.map(vas => translationMap[vas]);
        return {
            targets: targetNames,
            sources: sourceNames
        }
    })
}

export const ValidationResultMessageComponent = (props) => {
    return(
        <p className="g-redf-c h2">{props.message}</p>
    );
}

export const RequireRuleMessageComponent = (props) => {
    var preparedRules = prepareRules(props.brokenRules,props.translationMap);
    return(
        <div>
            <p className="g-redf-c h2">Naruszono relację wymagalności</p>
            {preparedRules.map((rule,idx) => {
                return <p key={idx}>Produkty {rule.sources} wymagają produktów {rule.targets}. Dodaj wymagane usługi, żeby kontynuować.</p>;
            })}
        </div>
    );
}

RequireRuleMessageComponent.propTypes = {
    brokenRules: PropTypes.arrayOf(PropTypes.shape({
        sourceVases: PropTypes.arrayOf(PropTypes.string),
        targetVases: PropTypes.arrayOf(PropTypes.string),
        type: PropTypes.string
    })).isRequired,
    translationMap: PropTypes.object.isRequired
}

export const DisableRuleMessageComponent = (props) => {
    var preparedRules = preparedRules(props.brokenRules,props.translationMap);
    return(
        <div>
            <p className="g-redf-c h2">Naruszono relację wykluczania</p>
            {preparedRules.map((rule,idx) => {
                return <p key={idx}>Produkty {rule.sources} wykluczają się z produktami {rule.targets}. Usuń wykluczające się usługi, żeby kontynuować.</p>;
            })}
        </div>
    );
}

DisableRuleMessageComponent.propTypes = {
   brokenRules: PropTypes.arrayOf(PropTypes.shape({
       sourceVases: PropTypes.arrayOf(PropTypes.string),
       targetVases: PropTypes.arrayOf(PropTypes.string),
       type: PropTypes.string
   })).isRequired,
   translationMap: PropTypes.object.isRequired
}

